/**
 * 获取本地mock的测试数据
 */
import { getQueryParams } from './url-utils';

const request = (opt) => {
  return new Promise((resolve, reject) => {
    try {
      if (opt.url.indexOf('?') !== -1) {
        // 处理get请求
        let { url } = opt;
        let _url = url.split('?');
        opt.data = getQueryParams(_url[1]);
        opt.url = _url[0];
      }
      opt.url = /^\//.test(opt.url) ? opt.url : '/' + opt.url; // 处理些许没有 ‘/’ 字符开头的接口
      if (opt.url === '/fintech/v1/dict/query') {
        switch (opt.data.dictType) {
          case 'BusinessScope':
            opt.url = '/fintech/v1/dict/query-business-scope';
            break;
          case 'RuleDataType':
            opt.url = '/fintech/v1/dict/query-rule-daatatype';
            break;
          case 'AlignMode':
            opt.url = '/fintech/v1/dict/query-align-mode';
            break;
          case 'LocationTag':
            opt.url = '/fintech/v1/dict/query-location-tag';
            break;
          case 'LocationType':
            opt.url = '/fintech/v1/dict/query-location-type';
            break;
          case 'LineField':
            opt.url = '/fintech/v1/dict/query-line-field';
            break;
          case 'StatementType':
            opt.url = '/fintech/v1/dict/query-statement-type';
            break;
          case 'ElementType':
            opt.url = '/fintech/v1/dict/query-element-type';
            break;
          case 'MoveDirection':
            opt.url = '/fintech/v1/dict/query-move-direction';
            break;
          case 'StatementFileName':
            opt.url = '/fintech/v1/dict/query-statement-filename';
            break;
          case 'StatementField':
            opt.url = '/fintech/v1/dict/query-statement-field';
            break;
          case 'EntryField':
            opt.url = '/fintech/v1/dict/query-entry-field';
            break;
          case 'ExchangeMode':
            opt.url = '/fintech/v1/dict/queryExchangeMode';
            break;
          case 'Currency':
            opt.url = '/fintech/v1/dict/queryCurrency';
            break;
          case 'AssetType':
            opt.url = '/fintech/v1/bankAccount/queryAssetType';
            break;
          case 'ImportMode':
            opt.url = '/fintech/v1/dict/queryImportMode';
            break;
          case 'TimeUnit':
            opt.url = '/fintech/v1/dict/queryTimeUnit';
            break;
          case 'SettlementPeriod':
            opt.url = '/fintech/v1/dict/SettlementPeriod';
        }
      }
      if (opt.url === '/fintech/v1/settlement/dict/query') {
        switch (opt.data.type) {
          case 'SettlementPeriod':
            opt.url = '/fintech/v1/settlement/dict/SettlementPeriod';
            break;
          default:
            break;
        }
      }
      if (opt.url === '/fintech/v1/workFlow/data-dict/list') {
        switch (opt.data.dictCodes) {
          case 'priority':
            opt.url = '/fintech/v1/workFlow/dict/query-priority';
            break;
          case 'task-type':
            opt.url = '/fintech/v1/workFlow/dict/query-task-type';
            break;
          case 'task-state':
            opt.url = '/fintech/v1/workFlow/dict/query-task-state';
            break;
          case 'sort-time':
            opt.url = '/fintech/v1/workFlow/dict/query-sort-time';
            break;
          default:
            break;
        }
      }
      if (opt.url === '/fintech/v1/settlement/revenue/org/queryOrgRevenuePlans') {
        opt.url = '/fintech/v1/settlement/queryOrgRevenueSettlementPlans';
      }
      if (opt.url === '/fintech/v1/settlement/revenue/org/addBtnClickApi') {
        opt.url = '/fintech/v1/settlement/addBtnClickApi';
      }
      if (opt.url === '/fintech/v1/settlement/revenue/org/queryOrgRevenuePlanDetail') {
        opt.url = '/fintech/v1/settlement/queryOrgRevenueSettlementPlanDetai';
      }
      if (opt.url === '/fintech/v1/trust/currencyDisplay/query') {
        opt.url = '/fintech/v1/settlement/amountSysbol';
      }
      if (opt.url === '/fintech/v1/organization/list?shortCode=20002222') {
        opt.url = '/fintech/v1/organization/list';
      }
      if (opt.url === '/fintech/v1/orgOperator/list?pageNo=1&pageSize=10&shortCode=20002222&operatorId=18600051111') {
        opt.url = '/fintech/v1/orgOperator/list';
      }
      if (opt.url === '/fintech/v1/workflow/process/details') {
        opt.url = '/fintech/v1/settlement/queryWorkflowDetail';
      }
      if (opt.url === '/fintech/v1/group/info') {
        opt.url = '/fintech/v1/group/groupInfo';
      }
      if (opt.url === '/fintech/v1/settlement/revenue/org/queryOrgAndProfile') {
        switch (opt.data.orgShortCode) {
          case '%1':
            opt.url = '/fintech/v1/settlement/queryOrgAndProfile';
            break;
          case '%2':
            opt.url = '/fintech/v1/settlement/queryOrgAndProfile68';
            break;
          default:
            opt.url = '/fintech/v1/settlement/queryOrgAndProfiledefault';
            break;
        }
      }
      if (opt.url === '/fintech/v1/settlement/dict/query') {
        switch (opt.data.type) {
          case 'SettlementProcessStatus':
            opt.url = '/fintech/v1/settlement/settlementProcessStatus';
        }
      }
      if (opt.url === '/fintech/v1/till/queryOrgTills') {
        if (opt.data.pageSize === 1) {
          opt.url = '/fintech/v1/till/checkList';
        }
        if (opt.data.tillId) {
          opt.url = '/fintech/v1/till/queryTillDetauls';
        }
      }
      // 相同接口不同入参返回不同mock数据配置项，重置json文件
      import(`../../../debug-mock-data${opt.url}.json`)
        .then((data) => {
          data = data.default || data;
          resolve(data);
        })
        .catch(() => {
          resolve({
            header: {
              success: true,
            },
            list: [],
          });
        });
    } catch (e) {
      resolve();
      // reject(e);
    }
  });
};

export default request;
